
import axios from 'axios'
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import cookie from 'vux/src/tools/cookie/index.js'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'


export default {
components: {
XHeader,
Flexbox,
FlexboxItem,Cell,Group,XButton,XTable
},
data() {
return {
number1:1,
number2:1,
userId:cookie.get('userId'),
startTime1:'',
activePace:'',
activeCreatePeople:'',
activeId:'',
activeContext:'',

activeComplete:[]



};
},
methods:{
getActivity(){
axios({
method: 'get',
url: '/dangjian/active/getRunningActive',
params: {
pageNum:1,
pageSize:1
}
}) .then((res)=> {
console.log(res.data)
this.startTime1=res.data.list[0].startTime;
this.activePace=res.data.list[0].activePace;
this.activeCreatePeople=res.data.list[0].activeCreatePeople;
this.activeContext=res.data.list[0].activeContext;
this.activeId=res.data.list[0].id;
})
.catch(function (error) {
console.log(error);
});


},
gettimes1(){
axios({
method: 'post',
url: '/dangjian/active/getParticipateCount',
params: {
userId:this.userId,
activeType:3
}
}) .then((res)=> {
console.log(res)
this.number1=res.data


})
.catch(function (error) {
console.log(error);
});


}
,
gettimes2(){
axios({
method: 'post',
url: '/dangjian/active/getParticipateCount',
params: {
userId:this.userId,
activeType:5
}
}) .then((res)=> {
console.log(res)
this.number2=res.data


})
.catch(function (error) {
console.log(error);
});


},submit(){

axios({
method: 'post',
url: '/dangjian/active/participate',
params: {
userId:this.userId,
activeId:this.activeId
}
}) .then((res)=> {
alert(res.msg)



})
.catch(function (error) {
console.log(error);
});

},
getAlreadyActive(){
axios({
method: 'get',
url: '/dangjian/active/getAlreadyActive',
params: {
pageNum:1,
pageSize:4
}
}) .then((res)=> {
console.log("112132132132131",res.data.list)
this.activeComplete=res.data.list
console.log(this.activeComplete)
})
.catch(function (error) {
console.log(error);
});


},

},
mounted() {
this.getActivity();
this.gettimes1();
this.gettimes2();
this.getAlreadyActive()
}
};



